import {action} from 'typesafe-actions'

import {AuthActionTypes as Types} from './configStore'
import {ISignInPayload, ISignUpPayload} from '../../ts'

/* Sign In Actions */
export const $googleSignInSaga = () => action(Types.SAGA_SIGN_IN_GOOGLE)
export const $signInSaga = (payload: Partial<ISignInPayload>) => action(Types.SAGA_SIGN_IN, payload)
export const $signInSuccess = (payload: any) => action(Types.SUCCESS_SIGN_IN, payload)
export const $signInFailure = (payload: any) => action(Types.FAILURE_SIGN_IN, payload)

/* Sign Up Actions */
export const $signUpSaga = (payload: Partial<ISignUpPayload>) => action(Types.SAGA_SIGN_UP, payload)
export const $signUpSuccess = (payload: any) => action(Types.SUCCESS_SIGN_UP, payload)
export const $signUpFailure = (payload: any) => action(Types.FAILURE_SIGN_UP, payload)

/* Login Auth Actions */
export const $logout = () => action(Types.LOGOUT)
export const $loginSign = () => action(Types.LOGIN_SIGN)
