import React, {FC, useState, useCallback} from 'react'

import {AppBar, Drawer} from '../../organisms'

import {useAppLayoutStyles} from './styles'

/**
 * AppLayout
 * **/
export const AppLayout: FC = ({children}) => {
  /** hooks **/
  const classes = useAppLayoutStyles()

  /** states **/
  const [isShowDrawer, setShowDrawer] = useState(false)

  /** event handlers **/
  const showDrawer = useCallback(() => {
    setShowDrawer(true)
  }, [])
  const hideDrawer = useCallback(() => {
    setShowDrawer(false)
  }, [])

  /** render **/
  return (
    <div className={classes.root}>
      <Drawer isShow={isShowDrawer} hideDrawer={hideDrawer} />
      <AppBar isShow={isShowDrawer} showDrawer={showDrawer} hideDrawer={hideDrawer} />
      {children}
    </div>
  )
}

/**
 * Audit
 * **/
AppLayout.whyDidYouRender = true
AppLayout.displayName = 'AppLayout *Layout*'
