import React, {FC, useEffect} from 'react'
import {Provider} from 'react-redux'
import Cookies from 'js-cookie'
import {ThemeProvider} from 'styled-components'
import {ConnectedRouter} from 'connected-react-router'
import {ThemeProvider as MuiProvider} from '@material-ui/core/styles'

import RootRouter from '../routes'
import {googleService} from '../services'
import {store, history} from '../store'
import {authUrls, request} from '../api'
import {GlobalStyled, StyledTheme, MuiTheme} from '../styles'

/**
 * App
 * **/
const App: FC = () => {
  /** hooks **/

  const f1 = async () => {
    const cookie = Cookies.get('authCookie')

    if (cookie) {
      const apiCall = await request(authUrls.login, {})
      console.log(apiCall)
    }
  }
  /** effects **/
  useEffect(() => {
    googleService()
    f1()
  }, []) // $SetUser, UserStore

  /** render **/
  return (
    <Provider store={store}>
      <ThemeProvider theme={StyledTheme}>
        <MuiProvider theme={MuiTheme}>
          <ConnectedRouter noInitialPop history={history}>
            <RootRouter />
          </ConnectedRouter>
        </MuiProvider>
      </ThemeProvider>
      <GlobalStyled />
    </Provider>
  )
}

/**
 * Audit
 * **/
App.displayName = 'App'
App.whyDidYouRender = true

export default App
