import React, {FC} from 'react'
import {useHistory} from 'react-router-dom'
import clsx from 'clsx'

import Toolbar from '@material-ui/core/Toolbar'
import AppBarMui from '@material-ui/core/AppBar'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'

import MenuIcon from '@material-ui/icons/Menu'
import CloseIcon from '@material-ui/icons/Close'
import ExitToAppIcon from '@material-ui/icons/ExitToApp'

import {useAppBarStyles} from './styles'
import {IAppBar} from './ts'

/**
 * AppBar
 * **/
export const AppBar: FC<IAppBar> = ({isShow, showDrawer, hideDrawer}) => {
  /** hooks **/
  const history = useHistory()
  const classes = useAppBarStyles()

  /** event handlers **/
  const goToAuth = () => history.push('/login')
  const handleClickLink = isShow ? hideDrawer : showDrawer

  /** constants **/
  const icon = isShow ? <CloseIcon /> : <img src="/favicon.png" alt="logo" style={{maxWidth: 32, maxHeight: 32, position: 'relative'}} />
  const appClassName = clsx(classes.appBar, {[classes.appBarShift]: isShow})

  /** render **/
  return (
    <AppBarMui position="fixed" className={appClassName}>
      <Toolbar className={classes.toolbar}>
        <IconButton color="inherit" aria-label="open drawer" onClick={handleClickLink} style={{padding: 6, marginLeft: 5}}>
          {icon}
        </IconButton>
        <div className={classes.buttons}>
          <IconButton color="inherit" aria-label="open account" onClick={goToAuth}>
            <ExitToAppIcon />
          </IconButton>
        </div>
      </Toolbar>
    </AppBarMui>
  )
}

/**
 * Audit
 * **/
AppBar.whyDidYouRender = true
AppBar.displayName = 'AppBar *Organism*'
