import {all, fork, put, call, takeEvery} from 'redux-saga/effects'
import {toast} from 'react-toastify'
import Cookies from 'js-cookie'
import {omit} from 'lodash'

import {AuthActionTypes} from './configStore'
import {request, authUrls} from '../../api'
import {googleSign} from '../../services'
import {$signInFailure, $signInSuccess, $signUpFailure, $signUpSuccess} from './actions'
import {ISignUpAction, ISignInAction, googleProfileT} from '../../ts'

/* Sagas */
function* logout() {
  try {
    Cookies.remove('user')
  } catch (err) {
    toast.error('🔥LOGIN ERROR🔥 ***logout saga***')
    yield put($signInFailure({msg: '🔥LOGIN ERROR🔥 ***logout saga***'}))
  }
}

function* loginRequest() {
  try {
    const apiCall = yield call(request, authUrls.login, {method: 'get'})

    if (!apiCall.success) {
      toast.warn(apiCall.message)
      yield put($signInFailure({msg: `${apiCall.message}`}))
    } else {
      yield put($signInSuccess(omit(apiCall.data, ['_id', 'password', 'repeatPassword'])))
    }
  } catch (err) {
    toast.error('🔥LOGIN ERROR🔥 ***loginRequest saga***')
    yield put($signInFailure({msg: '🔥LOGIN ERROR🔥 ***loginRequest saga***'}))
  }
}

function* signInSaga({payload}: ISignInAction) {
  try {
    const apiCall = yield call(request, authUrls.signIn, {
      body: payload
    })

    console.log(apiCall)

    if (!apiCall.ok) {
      toast.warn(apiCall.message)
      yield put($signInFailure({msg: `${apiCall.message}`}))
    } else {
      const apiData = yield apiCall.json()
      Cookies.set('authCookie', apiData.cookie)
      yield put($signInSuccess(apiData.data))
    }
  } catch (err) {
    toast.error(`🔥SIGN_IN ERROR 🔥 ***signInRequest saga***`)
    yield put($signInFailure({msg: '🔥SIGN_IN ERROR🔥 ***signInRequest saga***'}))
  }
}

function* $signInGoogleSaga() {
  try {
    const apiCall: googleProfileT = yield call(googleSign)

    if (apiCall.email) {
      const {email, fullName, ...rest} = apiCall

      console.log(apiCall)
      // Cookies.set('user', apiCall.id)
      yield put($signUpSuccess({email, name: fullName, googleProfile: rest}))
    }
  } catch (err) {
    toast.error('🔥 GOOGLE LOGIN ERROR🔥 ***$signInGoogleSaga saga***')
  }
}

function* signUpSaga({payload}: ISignUpAction) {
  try {
    const apiCall = yield call(request, authUrls.signUp, {
      body: payload
    })

    if (!apiCall.success) {
      toast.warn(apiCall.message)
      yield put($signUpFailure({msg: apiCall.message}))
    } else {
      Cookies.set('user', apiCall.data.token)
      yield put($signUpSuccess(apiCall.data))
    }
  } catch (err) {
    toast.error('🔥SIGN_UP ERROR🔥 ***signUpRequest saga***')
    yield put($signUpFailure({msg: '🔥SIGN_UP ERROR🔥 ***signInRequest saga***'}))
  }
}

function* $signUpGoogleSaga() {
  try {
    const apiCallProfile: googleProfileT = yield call(googleSign)

    if (apiCallProfile.email) {
      const {email, fullName, ...rest} = apiCallProfile
      const apiCallSignUp = yield call(request, authUrls.signUpGoogle, {
        body: apiCallProfile
      })

      console.log(1, apiCallProfile)
      console.log(2, apiCallSignUp)

      const {googleID, googleToken} = apiCallProfile
      const newUser = {
        email,
        fullName,
        googleID,
        googleToken,
        ...rest
      }
      // Cookies.set('user', apiCall.id)
      yield put($signUpSuccess(newUser))
    }
  } catch (err) {
    toast.error('🔥 GOOGLE LOGIN ERROR🔥 ***$signInGoogleSaga saga***')
  }
}

/* Watchers */
function* authWatch() {
  yield takeEvery(AuthActionTypes.SAGA_SIGN_UP, signUpSaga)
  yield takeEvery(AuthActionTypes.SAGA_SIGN_IN, signInSaga)
  yield takeEvery(AuthActionTypes.SAGA_SIGN_IN_GOOGLE, $signInGoogleSaga)
  yield takeEvery(AuthActionTypes.SAGA_SIGN_UP_GOOGLE, $signUpGoogleSaga)
}
function* loginWatch() {
  yield takeEvery(AuthActionTypes.LOGIN_SIGN, loginRequest)
  yield takeEvery(AuthActionTypes.LOGOUT, logout)
}

/* Forks */
export function* authSaga() {
  yield all([fork(authWatch)])
  yield all([fork(loginWatch)])
}
