import React, {FC} from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'

import {AppLayout} from '../components/layouts/app'

import {privateRoutes, publicRoutes} from './routes'

/**
 * RootRouter
 * **/
const RootRouter: FC = () => {
  /** render **/
  return (
    <AppLayout>
      <Switch>
        {/* Public Routes */}
        {publicRoutes.map(({path, component}) => (
          <Route key={`route-${path}`} exact path={path} component={component} />
        ))}
        {/* Private Routes */}
        {privateRoutes.map(
          ({path, isAuth, ...routeProps}) =>
            isAuth && <Route key={`private-route-${path}`} {...routeProps} path={path} />
        )}
        <Redirect from="*" to={{pathname: '/'}} />
      </Switch>
    </AppLayout>
  )
}

/**
 * Audit
 * **/
RootRouter.whyDidYouRender = true
RootRouter.displayName = 'RootRouter *Router*'

export default RootRouter
