import MailIcon from '@material-ui/icons/Mail'
import CodeIcon from '@material-ui/icons/Code'
import HomeIcon from '@material-ui/icons/Home'
import GitHubIcon from '@material-ui/icons/GitHub'
import SettingsIcon from '@material-ui/icons/Settings'
import ExitToAppIcon from '@material-ui/icons/ExitToApp'
import AccountCircleIcon from '@material-ui/icons/AccountCircle'

import {ILink, badgeProps} from '../ts/router'

/* Drawers */
const code = ({badge, badgeColor}: badgeProps): ILink => {
  return {
    badge,
    badgeColor,
    isAuth: true,
    to: '/code',
    text: 'code',
    Icon: CodeIcon
  }
}
const settings = ({badge, badgeColor}: badgeProps): ILink => {
  return {
    badge,
    badgeColor,
    to: '/settings',
    text: 'settings',
    Icon: SettingsIcon
  }
}
const gitHub = ({badge, badgeColor}: badgeProps): ILink => {
  return {
    badge,
    badgeColor,
    isAuth: true,
    to: '/gitHub',
    text: 'gitHub',
    Icon: GitHubIcon
  }
}

/* Accounts */
const exit = (handleExit: () => void): ILink => {
  return {
    to: '/',
    text: 'exit',
    onClick: handleExit,
    Icon: ExitToAppIcon
  }
}
const profile = ({badge, badgeColor}: badgeProps): ILink => {
  return {
    badge,
    badgeColor,
    to: '/profile',
    text: 'profile',
    Icon: AccountCircleIcon
  }
}
const messages = ({badge, badgeColor}: badgeProps): ILink => {
  return {
    badge,
    badgeColor,
    to: '/messages',
    text: 'messages',
    Icon: MailIcon
  }
}

/* App Links */
export const links = (codeBadge?: any, gitHubBadge?: any, settingsBadge?: any): ILink[] => {
  return [
    {
      to: '/',
      text: 'home',
      isAuth: true,
      Icon: HomeIcon
    },
    gitHub({...gitHubBadge}),
    code({...codeBadge}),
    settings({...settingsBadge})
  ]
}

export const drawerMobileLinks = (
  profileBadge: badgeProps,
  messagesBadge: badgeProps,
  events: any
): ILink[] => {
  return [profile(profileBadge), messages(messagesBadge), exit(events.handleExit)]
}

export const drawerDesktopLinks = (profileBadge: badgeProps, events: any): ILink[] => {
  return [profile(profileBadge), exit(events.handleExit)]
}
