import {Reducer} from 'redux'
import {LayoutState, LayoutActionTypes as Types} from './configStore'
import {ILayoutState} from '../../ts'

export const layoutReducer: Reducer<ILayoutState> = (state = LayoutState, action) => {
  switch (action.type) {
    case Types.SET: {
      return {...state, themeColor: action.payload}
    }
    default: {
      return state
    }
  }
}
