import React, {FC, Ref, forwardRef} from 'react'
import {Link, LinkProps} from 'react-router-dom'

import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'

import {useDrawerLinkStyle} from './styles'
import {ILink} from '../../../ts/router'

const MenuDrawerLink: FC<ILink> = ({text, to, Icon, onClick}) => {
  /** hooks **/
  const classes = useDrawerLinkStyle()

  /** components **/
  const forward: LinkProps['component'] = forwardRef((props, ref: Ref<HTMLDivElement>) => (
    <Link ref={ref} {...props} />
  ))

  /** render **/
  return (
    <ListItem button to={to} className={classes.link} component={forward} onClick={onClick}>
      <ListItemIcon>
        <Icon color="secondary" className={classes.icon} />
      </ListItemIcon>
      <ListItemText primary={text} />
    </ListItem>
  )
}

/* Audit */
MenuDrawerLink.whyDidYouRender = true
MenuDrawerLink.displayName = 'DrawerLink *Atom*'

export {MenuDrawerLink}
