import React, {FC, useState, useMemo, useEffect, useCallback} from 'react'
import {useDispatch} from 'react-redux'
import {pickBy, identity} from 'lodash'

import Grid from '@material-ui/core/Grid'
import Avatar from '@material-ui/core/Avatar'
import Button from '@material-ui/core/Button'
import CloseIcon from '@material-ui/icons/Close'
import Container from '@material-ui/core/Container'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import LockOutlinedIcon from '@material-ui/icons/LockOutlined'
import TextField, {TextFieldProps} from '@material-ui/core/TextField'

import {googleIcon} from '../../../constants'
import {initAuthFields, authState} from '../../../config/auth'
import {$signUpSaga, $signInSaga, $googleSignInSaga} from '../../../store/auth'

import {useAuthFormStyles, IconSocialStyled} from './styles'
import {IAuthLayout} from './ts'

const AuthLayout: FC<IAuthLayout> = ({hideForm}) => {
  /** hooks **/
  const dispatch = useDispatch()
  const classes = useAuthFormStyles()

  /** states **/
  const [key, setKey] = useState(1)
  const [isSignIn, setIsSignIn] = useState(true)
  const [fieldsState, setFieldsState] = useState(isSignIn ? authState.signIn : authState.signUp)

  /** event handlers **/
  const handleGoogleAuth = () => dispatch($googleSignInSaga())
  const handleFieldChange = useCallback((event) => {
    const {
      target: {value, name}
    } = event

    setFieldsState((prevState) => ({...prevState, [name]: value}))
  }, [])
  const handleToggleSignIn = useCallback(() => {
    setIsSignIn(!isSignIn)
    setKey(key + 1)
  }, [isSignIn, key])
  const handleSendForm = useCallback(() => {
    return isSignIn
      ? dispatch($signInSaga(pickBy(fieldsState, identity)))
      : dispatch($signUpSaga(pickBy(fieldsState, identity)))
  }, [dispatch, fieldsState, isSignIn])

  /** constants **/
  const getText = useMemo(() => {
    if (isSignIn) {
      return {
        button: 'Войти',
        goTo: 'Зарегистриирваться!',
        isHaveAccount: 'Нет аккаунта? '
      }
    }

    return {
      goTo: 'Войти!',
      button: 'Зарегистриирваться',
      isHaveAccount: 'Есть аккаунт? '
    }
  }, [isSignIn])

  /** memo components **/
  const Fields = useMemo(() => {
    const fields = isSignIn ? initAuthFields.signIn : initAuthFields.signUp

    return (
      <Grid container spacing={2}>
        {fields.map(({id, type, label, name, required, fullWidth}: TextFieldProps) => (
          <Grid item xs={12} sm={!fullWidth && 6} key={id}>
            <TextField
              fullWidth
              id={id}
              name={name}
              type={type}
              label={label}
              variant="outlined"
              required={required}
              onChange={handleFieldChange}
            />
          </Grid>
        ))}
      </Grid>
    )
  }, [handleFieldChange, isSignIn])

  /** effects **/
  useEffect(() => {
    setFieldsState(isSignIn ? authState.signIn : authState.signUp)
  }, [isSignIn])

  /** render **/
  return (
    <Container component="main" className={classes.root}>
      <IconButton className={classes.closeButton} onClick={hideForm}>
        <CloseIcon />
      </IconButton>
      <Avatar className={classes.avatar} children={<LockOutlinedIcon />} />
      <form noValidate key={key}>
        {Fields}
        <Button
          fullWidth
          color="primary"
          variant="contained"
          onClick={handleSendForm}
          children={getText.button}
          className={classes.sendButton}
        />
      </form>
      <IconSocialStyled src={googleIcon} onClick={handleGoogleAuth} />
      <div>
        <Typography component="span" variant="subtitle2" children={getText.isHaveAccount} />
        <Typography
          color="primary"
          component="span"
          variant="subtitle2"
          children={getText.goTo}
          onClick={handleToggleSignIn}
          className={classes.isAccountText}
        />
      </div>
    </Container>
  )
}

/**
 * Audit
 * **/
AuthLayout.whyDidYouRender = true
AuthLayout.displayName = '_**Layout**_AuthLayout'

export {AuthLayout}
