import {Reducer} from 'redux'

import {AuthState, AuthActionTypes as Types} from './configStore'
import {IAuthState} from '../../ts'

export const authReducer: Reducer<IAuthState> = (state = AuthState, action) => {
  switch (action.type) {
    /* Saga Starts */
    case Types.SAGA_SIGN_IN:
    case Types.SAGA_SIGN_UP: {
      return {...state, isFetch: true}
    }

    /* Success Types */
    case Types.SUCCESS_SIGN_IN:
    case Types.SUCCESS_SIGN_UP: {
      return {
        ...state,
        isAuth: true,
        isFetch: false,
        ...action.payload
      }
    }

    /* Failure Types */
    case Types.FAILURE_SIGN_IN:
    case Types.FAILURE_SIGN_UP: {
      return {...state, isFetch: false, error: action.payload}
    }

    /* Auth Login Types */
    case Types.LOGIN_SIGN: {
      return {
        ...state,
        isAuth: true,
        isFetch: false,
        ...action.payload
      }
    }
    case Types.LOGOUT: {
      return AuthState
    }

    /* Default */
    default: {
      return state
    }
  }
}
