import React, {FC, useMemo} from 'react'
import clsx from 'clsx'

import List from '@material-ui/core/List'
import DrawerMui from '@material-ui/core/Drawer'

import {links} from '../../../routes'

import {MenuDrawerLink} from '../../atoms/Links'

import {IIDrawer} from './ts'
import {useDrawerStyles} from './styles'

/**
 * Drawer Component
 * **/
const Drawer: FC<IIDrawer> = ({isShow, hideDrawer}) => {
  /** hooks **/
  const classes = useDrawerStyles()

  /** constants **/
  const clsxNames = {
    drawer: clsx(classes.drawer, {
      [classes.drawerOpen]: isShow,
      [classes.drawerClose]: !isShow
    }),
    classes: {
      paper: clsx(classes.paper, {
        [classes.drawerOpen]: isShow,
        [classes.drawerClose]: !isShow
      })
    }
  }
  const memoListComponent = useMemo(() => {
    return links({}, {}, {}).map(
      (linkProps, index) =>
        linkProps.isAuth && <MenuDrawerLink key={index} {...linkProps} onClick={hideDrawer} />
    )
  }, [hideDrawer])

  /** render **/
  return (
    <DrawerMui variant="permanent" className={clsxNames.drawer} classes={clsxNames.classes}>
      <List className={classes.list} children={memoListComponent} />
    </DrawerMui>
  )
}

/**
 * Audit
 * **/
Drawer.whyDidYouRender = true
Drawer.displayName = 'Drawer *Organism*'

export default Drawer
