import {serviceConst} from '../constants'

export const googleService = () => {
  const script = document.createElement('script')

  script.src = 'https://apis.google.com/js/platform.js'
  script.async = true
  script.defer = true
  script.onload = () =>
    window.gapi.load('auth2', () => {
      try {
        gapi.auth2.init({client_id: serviceConst.googleClientID})
      } catch (err) {
        console.log('🔥 Google SignIn ERROR 🔥', err)
      }
    })
  document.head.appendChild(script)
}
export const googleSign = async () => {
  try {
    const googleApiCall = await window.gapi.auth2.getAuthInstance().signIn()

    const {id_token} = googleApiCall.getAuthResponse()
    const id = googleApiCall.getBasicProfile().getId()
    const email = googleApiCall.getBasicProfile().getEmail()
    const fullName = googleApiCall.getBasicProfile().getName()
    const image = googleApiCall.getBasicProfile().getImageUrl()

    return {TokenID: id_token, email, fullName, image, id}
  } catch (err) {
    console.log('🔥 Google SignIn ERROR 🔥', err)
  }
}
